import React, {useEffect, useState} from 'react';
import {useDispatch, useSelector} from "react-redux";
import {cleareCocktail, getCocktailById} from "../../redux/action/dal";
import {Link, useParams} from "react-router-dom";

import './Details.css'

import logo from '../../images/logo.png'

import {Swiper, SwiperSlide} from "swiper/react";

import "swiper/css";
import "swiper/css/free-mode";
import "swiper/css/navigation";
import "swiper/css/thumbs";


import {FreeMode, Navigation, Thumbs} from "swiper";
import Form from "../Form/Form";


const Details = () => {
    const [thumbsSwiper, setThumbsSwiper] = useState(null);

    const {cocktail} = useSelector(state => state)
    console.log(cocktail)
    const {id} = useParams()
    const dispatch = useDispatch()
    useEffect(() => {
        dispatch(getCocktailById(id))
        return () => {
            dispatch(cleareCocktail())
        }
    }, [dispatch, id])


    const ingrsName = Object.entries(cocktail).filter(el => el[0].includes('strIngredient') && (el[1])).map(el => el[1])
    console.log(ingrsName)

    return (
        <section className='details'>
            <div className="container">
                <div className="logo">
                    <img src={logo} alt=""/>
                </div>
                <div className="row">
                    <div className='col-1'>
                        <div className="details__box">
                            <div className="details_info">
                                <p>{cocktail.strDrink}</p>
                                <img src={cocktail.strDrinkThumb} alt=""/>
                            </div>

                            <div className="cocktail__info">
                                <p>{cocktail.strInstructions}</p>
                            </div>
                            <div className='details__slider'>
                                <p>ingridients</p>
                                <Swiper
                                    style={{
                                        "--swiper-navigation-color": "#fff",
                                        "--swiper-pagination-color": "#000",
                                        width: '500px',
                                        height: '300px',
                                    }}
                                    loop={true}
                                    spaceBetween={50}
                                    navigation={true}
                                    thumbs={{swiper: thumbsSwiper}}
                                    modules={[FreeMode, Navigation, Thumbs]}
                                    className="mySwiper2"
                                >{ingrsName && ingrsName.map((el, index) => {
                                    return (
                                        <SwiperSlide key={index} className='main-slider'>
                                            <img src={`https://www.thecocktaildb.com/images/ingredients/${el}.png`}/>
                                            <Link to={`/ingr/${el}`}>
                                                <p>{el}</p>
                                            </Link>
                                        </SwiperSlide>
                                    )
                                })}
                                </Swiper>
                                <Swiper
                                    style={{height: '100px', width: '500px', marginTop: '20px'}}
                                    onSwiper={setThumbsSwiper}
                                    loop={true}
                                    spaceBetween={10}
                                    slidesPerView={5}
                                    freeMode={true}
                                    watchSlidesProgress={true}
                                    modules={[FreeMode, Navigation, Thumbs]}
                                    className="mySwiper"
                                >
                                    {ingrsName && ingrsName.map((el, index) => {
                                        return (
                                            <SwiperSlide key={index}>
                                                <img
                                                    src={`https://www.thecocktaildb.com/images/ingredients/${el}.png`}/>
                                            </SwiperSlide>
                                        )
                                    })}
                                </Swiper>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    );
};

export default Details;